import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Login from '@/components/offer/Login'
import Join from '@/components/offer/Join'
import Mypage from '@/components/offer/Mypage'
import Dashboard from '@/components/supervise/Dashboard'
import Profile from '@/components/supervise/Profile'
import Tables from '@/components/supervise/Tables'
import Maps from '@/components/supervise/Maps'


Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {path: '/',name: 'home',component: Home},
    {path: '/login', name: 'login', component: Login},
    {path: '/join', name: 'join', component: Join},
    {path: '/mypage', name: 'mypage', component: Mypage},
    {
      path: '/dashboard',
      name: 'Dashboard',
      component: Dashboard,
      props: { page: 1 },
      alias: '/'
    },
    {
      path: '/profile',
      name: 'Profile',
      props: { page: 2 },
      component: Profile
    },
    {
      path: '/tables',
      name: 'Tables',
      props: { page: 3 },
      component: Tables
    },
    {
      path: '/maps',
      name: 'Maps',
      props: { page: 4 },
      component: Maps
    }

  ]
}) 



